ound-color: transparent;\r\n  border: none;\r\n  font-size: 18px;\r\n  padding: 8px;\r\n  cursor: pointer;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n \r\n.app-bar .app-bar__brand {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n \r\n.app-bar .app-bar__brand h1 {\r\n  color: white;\r\n  text-transform: uppercase;\r\n  font-size: 22px;\r\n  user-select: none;\r\n}\r\n \r\n.app-bar .app-bar__navigation {\r\n  position: absolute;\r\n  top: 50px;\r\n  left: -180px;\r\n  width: 150px;\r\n  transition: all 0.3s;\r\n  padding: 8px;\r\n  background-color: #C68B59;\r\n  overflow: hidden;\r\n}\r\n \r\n.app-bar .app-bar__navigation.open {\r\n  left: 0;\r\n}\r\n \r\n.app-bar .app-bar__navigation ul li a {\r\n  display: inline-block;\r\n  text-decoration: none;\r\n  color: white;\r\n  padding: 8px;\r\n  margin-bottom: 5px;\r\n  width: 100%;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n \r\n/* hero jumbotron */\r\n.hero {\r\n  display: grid;\r\n  align-items: center;\r\n  min-height: 380px;\r\n  width: 100%;\r\n  text-align: center;\r\n  background-image: url(\"../public/images/heros/ibumuda.jpeg\");\r\n  background-position: center;\r\n  background-size: cover;\r\n  margin-top: auto;\r\n}\r\n\r\n.hero-inner {\r\n  margin: 0 auto;\r\n  max-width: 800px;\r\n}\r\n\r\n.hero-title {\r\n  font-weight: 400;\r\n  color:white;\r\n  font-size: 60px;\r\n  text-shadow: 2px 2px #C68B59;\r\n  font-weight: bolder;\r\n}\r\n\r\n.hero-tagline {\r\n  color: #C68B59;\r\n  margin-top: 16px;\r\n  font-size: 35px;\r\n  line-height: 48px;\r\n  font-weight: 300;\r\n  text-shadow: 2px 2px #fffdfa;\r\n  font-weight: bolder;\r\n}\r\n/* Search */\r\n.search-container {\r\n  max-width: 800px;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  padding: 16px;\r\n  border-radius: 5px;\r\n  display: flex;\r\n  position: sticky;\r\n  top: 10px;\r\n  margin-top: 10px;\r\n  margin-left: 25%;\r\n  margin-right: 25%;\r\n  \r\n}\r\n\r\n.search-container > input {\r\n  width: 75%;\r\n  padding: 16px;\r\n  border: 0;\r\n  border-bottom: 1px solid #C68B59;\r\n  font-weight: bold;\r\n  background-color: #F4DFD0;\r\n  \r\n  \r\n}\r\n\r\n.search-container > input:focus {\r\n  outline: 0;\r\n  border-bottom: 2px solid #C68B59;\r\n  \r\n}\r\n\r\n.search-container > input:focus::placeholder {\r\n  font-weight: bold;\r\n}\r\n\r\n.search-container > input::placeholder {\r\n  color: #C68B59;\r\n  font-weight: normal;\r\n}\r\n\r\n.search-container > button {\r\n  width: 23%;\r\n  cursor: pointer;\r\n  margin-left: auto;\r\n  padding: 16px;\r\n  background-color: #C68B59;\r\n  border: 0;\r\n  text-transform: uppercase;\r\n}\r\n\r\n\r\n\r\n/*\r\n  Main Content\r\n*/\r\n \r\nmain {\r\n  padding: 32px;\r\n  flex: 1;\r\n}\r\n \r\n.content {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n \r\n.content .content__heading {\r\n  font-weight: normal;\r\n  text-align: center;\r\n  margin: 10px;\r\n  padding: 10px;\r\n\r\n}\r\n \r\n \r\n/*\r\n  pafas\r\n*/\r\n \r\n.pafas {\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 16px;\r\n  margin: 32px 0;\r\n}\r\n \r\n \r\n/*\r\n  pafa Item\r\n*/\r\n \r\n.pafa-item {\r\n  width: 100%;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n}\r\n \r\n.pafa-item__header {\r\n  position: relative;\r\n}\r\n \r\n.pafa-item .pafa-item__header .pafa-item__header__poster {\r\n  width: 100%;\r\n}\r\n \r\n.pafa-item .pafa-item__header .pafa-item__header__rating {\r\n  position: absolute;\r\n  padding: 8px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: black;\r\n  color: white;\r\n}\r\n \r\n.pafa-item .pafa-item__header .pafa-item__header__rating .pafa-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n \r\n.pafa-item .pafa-item__content {\r\n  padding: 16px;\r\n  text-align: center;\r\n}\r\n \r\n.pafa-item .pafa-item__content h3 {\r\n  margin: 0 0 10px 0;\r\n}\r\n \r\n.pafa-item .pafa-item__content h3 a {\r\n  color: #db0000;\r\n  te